* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.6rem;
  color: #343;
}

/* Header */
.header {
  padding: 1.4rem 0 1rem 0;
  margin-bottom: 2rem;
  font-size: 1rem;
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.1);
}

/* utility classes */
.container {
  max-width: 88.75rem;
  padding: 0 2rem;
  margin: 0 auto;
}
.shadow {
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}
.ant-card-body {
  padding: 5px;
}
/* for flexbox */
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.flex {
  display: flex;
}
.flex-col {
  flex-direction: column;
}
.justify-center {
  justify-content: center;
}
.justify-between {
  justify-content: space-between !important;
}
.items-center {
  align-items: center;
}
.gap-05 {
  grid-gap: 0.5rem;
  gap: 0.5rem;
}
.gap-1 {
  grid-gap: 1rem;
  gap: 1rem;
}
.gap-205 {
  grid-gap: 2.5rem;
  gap: 2.5rem;
}
.gap-3 {
  grid-gap: 3em;
  gap: 3em;
}
.gap-6 {
  grid-gap: 6rem;
  gap: 6rem;
}

/* for grid */
.grid {
  display: grid;
}
.grid-4-cols {
  grid-template-columns: repeat(4, 1fr);
}

/* margin */
.mt-05 {
  margin-top: 0.5rem;
}
.my-2 {
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.mr-05 {
  margin-right: 0.5rem;
}
.mb-4 {
  margin-bottom: 4rem;
}

/* typo */
.bold {
  font-weight: 600;
}
.text-xl {
  font-size: 1.2rem;
  color: #555 !important;
}

.text-2xl {
  font-size: 3rem !important;
  color: #555 !important;
}

/* width ,height */
.h-full {
  height: 100%;
}

a:link,
a:visited {
  /* text-decoration: underline; */
  font-size: 1rem;
}

.translate-y-2 {
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}

